/**
 * Toggle display of text when checkbox is checked
 */

 const toggleTextOnChecked = () => {
  let checkbox = document.querySelectorAll('.js-display-checkbox input')
  const text = document.querySelector('.js-display-text')

  if(text) {
    if(checkbox) {
      checkbox.forEach(item => {
        item.addEventListener('click', () => {
          checkbox = document.querySelectorAll('.js-display-checkbox input')
  
          let isChecked = Array.prototype.slice.call(checkbox).some(x => x.checked)
  
          if(isChecked) {
            text.classList.add('hidden')
          } else {
            text.classList.remove('hidden')
          }
        })
      })
    }
  }
}

export default toggleTextOnChecked