/**
* Activate all checkbox on change
*/

const activateCheckboxOnSelect = () => {
  const select = document.querySelector('.js-checkbox-all')
  let checkbox = document.querySelectorAll('.js-checkbox-activate input')
  const button = document.querySelector('.js-enable-checkbox')

  if(select) {
    select.addEventListener('change', () => {
      let element = select as HTMLInputElement

      checkbox.forEach(item => {
        let itemElement = item as HTMLInputElement

        if(element.value === '' || element.value === '') {
          itemElement.checked = true
        } else {
          itemElement.checked = false
        }
      })

      if(button) {
        checkbox = document.querySelectorAll('.js-checkbox-activate input')
  
        let isChecked = Array.prototype.slice.call(checkbox).some(x => x.checked)

        if(isChecked) {
          button.classList.remove('disabled')
          button.classList.add('blue')
        } else {
          button.classList.add('disabled')
          button.classList.remove('blue')
        }
      }
    })

    if(checkbox) {
      checkbox.forEach(item => {
        item.addEventListener('click', () => {
          let selectElement = select as HTMLSelectElement
          
          selectElement.selectedIndex = 0
          select.classList.add('gray')
        })
      })
    }
  }
}

export default activateCheckboxOnSelect
