/**
 * Change the color of select if an option is chosen
 */

 const toggleSelect = () => {
  const select = document.querySelectorAll('select')

  if(select.length > 0) {
    select.forEach(item => {
      let options = item.querySelectorAll('option:disabled')
      let isDisabled = false

      options.forEach(optionItem => {
        let optionElement = optionItem as HTMLOptionElement

        if(optionElement.value === item.value) {
          isDisabled = true
        }
      })

      if(item.selectedIndex <= 0 && (item.value === '' || isDisabled)) {
        item.classList.add('gray')
      }

      item.addEventListener('change', () => {
        if(item.selectedIndex === 0 && item.value === '') {
          item.classList.add('gray')
        } else {
          item.classList.remove('gray')
        }
      })
    })
  }
}

export default toggleSelect
