/**
* Toggles the candidate name input on radio checked
*/

const toggleCandidateRadio = () => {
  let subcontent = document.querySelectorAll('.js-subcontent')

  if(subcontent.length > 0) {
    subcontent.forEach(itemContent => {
      const radio = itemContent.querySelectorAll('.js-candidate-toggle')

      if(radio) {
        radio.forEach(item => {
          let content = item.closest('.js-clone-content')?.querySelector('.js-candidate-content')

          if(!content) {
            content = itemContent.querySelector('.js-candidate-content')
          }

          item.addEventListener('click', () => {
            let itemElement = item as HTMLInputElement
            if(itemElement.value === '' || itemElement.value === '') {
              content?.classList.add('active')
            } else {
              content?.classList.remove('active')
            }
          })
        })
      }
    })
  } else {
    const radio = document.querySelectorAll('.js-candidate-toggle')
    const content = document.querySelector('.js-candidate-content')

    if(radio && content) {
      radio.forEach(item => {
        item.addEventListener('click', () => {
          let itemElement = item as HTMLInputElement
          if(itemElement.value === '' || itemElement.value === '') {
            content.classList.add('active')
          } else {
            content.classList.remove('active')
          }
        })
      })
    }
  }
}

export default toggleCandidateRadio