/**
* Adds and delete candidate names
*/
function setInputFilter(textbox: Element, inputFilter: (value: string) => boolean): void {
  ['input', 'keydown', 'keyup', 'mousedown', 'mouseup', 'select', 'contextmenu', 'drop'].forEach(function(event) {
    textbox.addEventListener(event, function(this: (HTMLInputElement | HTMLTextAreaElement) & {oldValue: string; oldSelectionStart: number | null, oldSelectionEnd: number | null}) {
      if (inputFilter(this.value)) {
        this.oldValue = this.value
        this.oldSelectionStart = this.selectionStart
        this.oldSelectionEnd = this.selectionEnd
      } else if (Object.prototype.hasOwnProperty.call(this, 'oldValue')) {
        this.value = this.oldValue
        if (this.oldSelectionStart !== null &&
          this.oldSelectionEnd !== null) {
          this.setSelectionRange(this.oldSelectionStart, this.oldSelectionEnd)
        }
      } else {
        this.value = ''
      }
    })
  })
}

const getDuplicateContent = (subcontent : any, content: any, labelString : string, noParenthesis? : boolean, secondLabelString? : string, hasDuplicatedName? : boolean) => {
  if(subcontent.length > 0) {
    subcontent.forEach((subcontentItem : any, subcontentIndex : number) => {
      const addButton = subcontentItem.querySelector('.js-add-ss')
      let label = subcontentItem.querySelectorAll('.js-label-ss')
      let secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')
      let html = content[subcontentIndex].querySelector('.js-duplicate')
      let inputClones : any = []
      let deleteButton = subcontentItem.querySelectorAll('.js-delete-ss')

      if(label) {
        label.forEach((item : any) => {
          labelString = item.innerHTML
        })
      }

      if(html) {
        html = html.innerHTML
      }

      if(deleteButton.length > 0) {
        deleteButton.forEach((deleteItem : any) => {
          deleteItem.addEventListener('click', () => {
            let deleteParent = deleteItem.closest('.js-duplicate')

            if(deleteParent) {
              deleteParent.remove()
            }
  
            label = subcontentItem.querySelectorAll('.js-label-ss')
            secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')
  
            label.forEach((labelItem : any, labelIndex : any) => {
              if(noParenthesis) {
                labelItem.innerHTML = labelString.replace(/(\d+)(?!.*\d)/g, String(labelIndex + 1))
              } else {
                labelItem.innerHTML = labelString.replace(/(\d+)(?!.*\d)/g, String(labelIndex + 1))
              }
              if(secondLabel[labelIndex]) {
                secondLabel[labelIndex].innerHTML = secondLabelString + '(' + (labelIndex + 1) + ')'
              }
            })

            let currentDelete = subcontentItem.querySelectorAll('.js-delete-ss')

            if(currentDelete.length === 1) {
              currentDelete.forEach((item : any) => {
                item.classList.add('hide')
              })
            }
  
            if(hasDuplicatedName) {
              let mainParent = content[subcontentIndex].querySelectorAll('.js-duplicate')
  
              mainParent.forEach((cloneItem : any, cloneIndex : number) => {
                let inputs = cloneItem.querySelectorAll('.has-name')
  
                inputs.forEach((inputItem : any, inputIndex : number) => {
                  inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                })
              })
            }
          })
        })
      }

      if(addButton) {
        addButton.addEventListener('click', () => {
          deleteButton = subcontentItem.querySelectorAll('.js-delete-ss')

          deleteButton.forEach((item : any) => {
            item.classList.remove('hide')
          })

          let clone = document.createElement('div')

          if(clone && content[subcontentIndex]) {
            clone.classList.add('form__select-duplicate')
            clone.classList.add('js-duplicate')
            clone.innerHTML = html

            let deleteClone = clone.querySelector('.js-delete-ss')
            let inputNumber = clone.querySelectorAll('.js-input-number')
            let inputs = clone.querySelectorAll('input, textarea')
            let inputName = clone.querySelectorAll('.has-name')

            let question = clone.querySelectorAll('.question')

            if(question.length > 0) {
              question.forEach(questionItem => {
                questionItem.classList.add('hide')
              })
            }

            if(inputNumber.length > 0) {
              inputNumber.forEach(item => {
                let itemElement = item as HTMLInputElement
                itemElement.type = 'text'
                setInputFilter(item, value => {
                  return /^[0-9]*$/.test(value)
                })
              })
            }

            let index = 0

            inputs.forEach((inputItem : any) => {
              if(!(inputItem.getAttribute('type') === 'radio' || inputItem.getAttribute('type') === 'checkbox')) {
                inputItem.value = ''
              }

              if(hasDuplicatedName && inputItem.name) {
                inputClones[index] = inputItem.name
                inputItem.classList.add('has-name')
                inputItem.name = ''
                index = index + 1
              }
            })

            if(inputName.length > 0) {
              inputName.forEach(item => {
                item.classList.remove('has-name')
                item.classList.add('has-subname')
              })
            }

            if(deleteClone) {
              deleteClone.classList.remove('hide')

              deleteClone.addEventListener('click', () => {
                if(clone) {
                  clone.remove()
                }

                label = subcontentItem.querySelectorAll('.js-label-ss')
                secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')

                label.forEach((labelItem : any, labelIndex : any) => {
                  if(noParenthesis) {
                    labelItem.innerHTML = labelString.replace(/(\d+)(?!.*\d)/g, String(labelIndex + 1))
                  } else {
                    labelItem.innerHTML = labelString.replace(/(\d+)(?!.*\d)/g, String(labelIndex + 1))
                  }
                  if(secondLabel[labelIndex]) {
                    secondLabel[labelIndex].innerHTML = secondLabelString + '(' + (labelIndex + 1) + ')'
                  }
                })

                let currentDelete = subcontentItem.querySelectorAll('.js-delete-ss')

                if(currentDelete.length === 1) {
                  currentDelete.forEach((item : any) => {
                    item.classList.add('hide')
                  })
                }

                if(hasDuplicatedName) {
                  let mainParent = content[subcontentIndex].querySelectorAll('.js-duplicate')
    
                  mainParent.forEach((cloneItem : any, cloneIndex : number) => {
                    let inputs = cloneItem.querySelectorAll('.has-name')
    
                    inputs.forEach((inputItem : any, inputIndex : number) => {
                      inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                    })
                  })
                }
              })
            }

            content[subcontentIndex].appendChild(clone)

            if(hasDuplicatedName) {
              let mainParent = content[subcontentIndex].querySelectorAll('.js-duplicate')

              mainParent.forEach((cloneItem : any, cloneIndex : number) => {
                let inputs = cloneItem.querySelectorAll('.has-name')

                inputs.forEach((inputItem : any, inputIndex : number) => {
                  inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                })
              })
            }
          }

          label = subcontentItem.querySelectorAll('.js-label-ss')
          secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')

          label.forEach((labelItem : any, labelIndex : any) => {
            if(noParenthesis) {
              labelItem.innerHTML = labelString.replace(/(\d+)(?!.*\d)/g, String(labelIndex + 1))
            } else {
              labelItem.innerHTML = labelString.replace(/(\d+)(?!.*\d)/g, String(labelIndex + 1))
            }
            if(secondLabel[labelIndex]) {
              secondLabel[labelIndex].innerHTML = secondLabelString + '(' + (labelIndex + 1) + ')'
            }
          })
        })
      }
    })
  }
}

const duplicateContent = () => {
  let subcontent = document.querySelectorAll('.js-single-selection')
  let content = document.querySelectorAll('.js-content-ss')

  getDuplicateContent(subcontent, content, ' ', false, ' ')


  let subcontentMultiple = document.querySelectorAll('.js-multiple-selection')
  let contentMultiple = document.querySelectorAll('.js-content-ms')

  getDuplicateContent(subcontentMultiple, contentMultiple, ' ')

  let subcontentSingleFreeInput = document.querySelectorAll('.js-single-selection-free-input')
  let contentSingleFreeInput = document.querySelectorAll('.js-content-ssfi')

  getDuplicateContent(subcontentSingleFreeInput, contentSingleFreeInput, ' ', false, ' ')

  let subcontentMultipleFreeInput = document.querySelectorAll('.js-multiple-selection-free-input')
  let contentMultipleFreeInput = document.querySelectorAll('.js-content-msfi')

  getDuplicateContent(subcontentMultipleFreeInput, contentMultipleFreeInput, ' ')

  let subcontentMatrixSingleSelection = document.querySelectorAll('.js-matrix-single-selection')
  let contentMatrixSingleSelection = document.querySelectorAll('.js-content-mss')

  getDuplicateContent(subcontentMatrixSingleSelection, contentMatrixSingleSelection, ' ')

  let subcontentSingleSelectionChild = document.querySelectorAll('.js-single-selection-child')
  let contentSingleSelectionChild = document.querySelectorAll('.js-content-ssc')

  getDuplicateContent(subcontentSingleSelectionChild, contentSingleSelectionChild, ' ')

  let contact = document.querySelectorAll('.js-contact-container')
  let contactContent = document.querySelectorAll('.js-contact-content')

  getDuplicateContent(contact, contactContent, ' ', true, '', true)

  let delivery = document.querySelectorAll('.js-delivery-container')
  let deliveryContent = document.querySelectorAll('.js-delivery-content')

  getDuplicateContent(delivery, deliveryContent, ' ', true, '', true)

  let contact2 = document.querySelectorAll('.js-contact-container-2')
  let contactContent2 = document.querySelectorAll('.js-contact-content-2')

  getDuplicateContent(contact2, contactContent2, ' ', true, '', true)

  let delivery2 = document.querySelectorAll('.js-delivery-container-2')
  let deliveryContent2 = document.querySelectorAll('.js-delivery-content-2')

  getDuplicateContent(delivery2, deliveryContent2, ' ', true, '', true)

  let contact3 = document.querySelectorAll('.js-contact-container-3')
  let contactContent3 = document.querySelectorAll('.js-contact-content-3')

  getDuplicateContent(contact3, contactContent3, ' ', true, '', true)

  let delivery3 = document.querySelectorAll('.js-delivery-container-3')
  let deliveryContent3 = document.querySelectorAll('.js-delivery-content-3')

  getDuplicateContent(delivery3, deliveryContent3, ' ', true, '', true)

  let confirm = document.querySelectorAll('.js-confirm-container')
  let confirmContent = document.querySelectorAll('.js-confirm-content')

  getDuplicateContent(confirm, confirmContent, ' ', true, '', true)
}

export default duplicateContent