/**
* Toggles display of element
*/

const toggleShowHide = () => {
  const toggle = document.querySelectorAll('.js-toggle-hide')
  const show = document.querySelectorAll('.js-toggle-show')

  if(toggle.length > 0) {
    toggle.forEach(item => {
      item.addEventListener('click', () => {
        let nextElement = item.nextElementSibling

        if(show.length > 0) {
          let parentElement = item.parentElement

          if(parentElement) {
            nextElement = parentElement.nextElementSibling
          }
        }

        if(nextElement) {
          nextElement.classList.toggle('hide')
        }

        item.classList.toggle('toggle')
      })
    })
  }
}

export default toggleShowHide
