/**
* Toggles the radio content
*/

const toggleRadio = () => {
  let subcontent = document.querySelectorAll('.js-tab-content')

  subcontent.forEach(subcontentItem => {
    const advanceRadio = subcontentItem.querySelectorAll('.js-advance-toggle');
    const advanceContent = subcontentItem.querySelectorAll('.js-advance-wrapper');

    if(advanceRadio.length > 0 && advanceContent.length > 0) {
      advanceRadio.forEach(item => {
        item.addEventListener('click', () => {
          let itemElement = item as HTMLInputElement
          if(itemElement.value === '') {
            for(var x = 0; x < advanceContent.length; x++) {
              advanceContent[x].classList.add('display');
            }
          } else {
            for(var x = 0; x < advanceContent.length; x++) {
              advanceContent[x].classList.remove('display');
            }
          }
        })
      })    
    }

    const receivingRadio = subcontentItem.querySelectorAll('.js-receiving-toggle');
    const receivingContent = subcontentItem.querySelectorAll('.js-receiving-wrapper');

    if(receivingRadio.length > 0 && receivingContent.length > 0) {
      receivingRadio.forEach(item => {
        item.addEventListener('click', () => {
          let itemElement = item as HTMLInputElement
          if(itemElement.value === '') {
            for(var x = 0; x < receivingContent.length; x++) {
              receivingContent[x].classList.add('display');
            }
          } else {
            for(var x = 0; x < receivingContent.length; x++) {
              receivingContent[x].classList.remove('display');
            }
          }
        })
      })    
    }

    const informationRadio = subcontentItem.querySelectorAll('.js-information-toggle');
    const informationContent = subcontentItem.querySelectorAll('.js-information-wrapper');

    if(informationRadio.length > 0 && informationContent.length > 0) {
      informationRadio.forEach(item => {
        item.addEventListener('click', () => {
          let itemElement = item as HTMLInputElement
          if(itemElement.value === '') {
            for(var x = 0; x < informationContent.length; x++) {
              informationContent[x].classList.add('display');
            }
          } else {
            for(var x = 0; x < informationContent.length; x++) {
              informationContent[x].classList.remove('display');
            }
          }
        })
      })    
    }
  })
}

export default toggleRadio
