/**
 * Enables button when checkbox is checked
 */

const enableButtonOnChecked = () => {
  let checkbox = document.querySelectorAll('.js-enable-button input')
  const button = document.querySelector('.js-enable-checkbox')

  if(button) {
    if(checkbox) {
      checkbox.forEach(item => {
        item.addEventListener('click', () => {
          checkbox = document.querySelectorAll('.js-enable-button input')
  
          let isChecked = Array.prototype.slice.call(checkbox).some(x => x.checked)
  
          if(isChecked) {
            button.classList.remove('disabled')
            button.classList.add('blue')
          } else {
            button.classList.add('disabled')
            button.classList.remove('blue')
          }

          let parentElement = item.closest('li')
          let inputItem = item as HTMLInputElement
          
          if(inputItem.checked) {
            if(parentElement) {
              //@ts-ignore
              Sortable.utils.select(parentElement)
            }
          } else {
            if(parentElement) {
              //@ts-ignore
              Sortable.utils.deselect(parentElement)
            }
          }
        })
      })
    }
  }
}

export default enableButtonOnChecked