/**
* Toggles the checkbox in modal
*/

const toggleCheckbox = () => {
  const toggleCheckbox = document.querySelectorAll('.js-checkbox')
  let isAllChecked : any = []
  let startingIndex : any = []

  if(toggleCheckbox.length > 0) {
    toggleCheckbox.forEach((item, index) => {
      isAllChecked[index] = 0
      startingIndex[index] = 0
  
      item.addEventListener('click', () => {
        let itemElement = item.querySelector('input') as HTMLInputElement

        if(itemElement) {
          let parent = item.parentElement
          let sibling
          let inputRadio

          if(parent) {
            sibling = parent.nextElementSibling

            if(sibling) {
              inputRadio = sibling.querySelectorAll('input')
              startingIndex[index] = 0
            } else {
              inputRadio = parent.querySelectorAll('input')
              startingIndex[index] = 1
            }
          }

          if(inputRadio && inputRadio.length > 0) {
            inputRadio.forEach(radioItem => {
              if(itemElement.checked) {
                radioItem.checked = true
                if(startingIndex[index] === 0) {
                  isAllChecked[index] = inputRadio.length
                } else {
                  isAllChecked[index] = inputRadio.length - 1
                }
              } else {
                radioItem.checked = false
                isAllChecked[index] = 0
              }
            })
          }
        }

        let checkbox = document.querySelectorAll('.js-enable-button input')
        const button = document.querySelector('.js-enable-checkbox')
  
        let isChecked = Array.prototype.slice.call(checkbox).some(x => x.checked)

        if(button) {
          if(isChecked) {
            button.classList.remove('disabled')
            button.classList.add('blue')
          } else {
            button.classList.add('disabled')
            button.classList.remove('blue')
          }
        }
      })

      let itemElement = item.querySelector('input') as HTMLInputElement

      if(itemElement) {
        let parent = item.parentElement
        let sibling
        let inputRadio : any

        if(parent) {
          sibling = parent.nextElementSibling

          if(sibling) {
            inputRadio = sibling.querySelectorAll('input')
            startingIndex[index] = 0
          } else {
            inputRadio = parent.querySelectorAll('input')
            startingIndex[index] = 1
          }
        }

        if(inputRadio && inputRadio.length > 0) {
          inputRadio.forEach((radioItem : any, subindex : any) => {
            if(subindex >= startingIndex[index]) {
              radioItem.addEventListener('click', () => {
                if(radioItem.checked) {
                  isAllChecked[index] += 1
                } else {
                  isAllChecked[index] -= 1
                }

                if(startingIndex[index] === 0) {
                  if(isAllChecked[index] <= inputRadio.length - 1) {
                    itemElement.checked = false
                  } else {
                    itemElement.checked = true
                  }
                } else {
                  if(isAllChecked[index] < inputRadio.length - 1) {
                    itemElement.checked = false
                  } else {
                    itemElement.checked = true
                  }
                }
              })
            }
          })
        }
      }
    })
  }
}

export default toggleCheckbox
