/**
* Change the position of table on select change
*/

const changePosition = () => {
  let select = document.querySelectorAll('.js-change-position')
  let itemTr = document.querySelectorAll('.js-tr')
  let tableNumber = document.querySelectorAll('.js-table-number')
  let tbody = document.querySelector('.js-tbody')

  if(select.length > 0 && itemTr && tableNumber && tbody) {
    let options = {
      valueNames: [ { name: 'js-table-number', attr: 'data-id' } ]
    };
    //@ts-ignore
    let tableList = new List('js-sort-table', options)
  
    select.forEach(item => {
      item.addEventListener('change', () => {
        itemTr = document.querySelectorAll('.js-tr')

        let itemElement = item as HTMLSelectElement
        let itemParent = itemElement.closest('.js-tr')
        let swapElement = itemTr[Number(itemElement.value) - 1]

        if(itemParent && swapElement) {
          let selectedNumber = itemParent.querySelector('.js-table-number')

          if(selectedNumber) {
            if(Number(selectedNumber.innerHTML) < Number(itemElement.value)) {
              selectedNumber.setAttribute('data-id', itemElement.value + 'c')
            } else {
              selectedNumber.setAttribute('data-id', itemElement.value + 'a')
            }
          }

          tableList.reIndex()

          tableList.sort('js-table-number', { order: 'asc' })

          tableNumber = document.querySelectorAll('.js-table-number')

          tableNumber.forEach((tableItem, index) => {
            tableItem.innerHTML = String(index + 1)
            tableItem.setAttribute('data-id', String(index + 1) + 'b')
            let selectedIndexMain = tableItem.closest('.js-tr')?.querySelector('select') as HTMLSelectElement
            selectedIndexMain.selectedIndex = index
          })
        }
      })
    })
  }
}

export default changePosition