/**
* Header
*/

const header = () => {
  const header = document.querySelector('.header')
  const menu = document.querySelector('.header__menu');
  const loginNavigation = document.querySelector('.login__navigation');
  const loginHeader = document.querySelector('.login__header');
  const scrollHeader = document.querySelector('.js-header-scroll');
  const hamburger = document.querySelector('.header__hamburger');
  const dropdownS = document.querySelector('.dropdownSecondary');
  const dropdownP = document.querySelectorAll('.dropdownPrimary');
  const sublistS = document.querySelector('.header__sublist--secondary');
  const sublistP = document.querySelectorAll('.header__sublist--primary');
  const SP = window.matchMedia("(max-width: 767px)");
  const PC = window.matchMedia("(min-width: 768px)");
  
  if(PC.matches) {
    if(dropdownS && sublistS) {
      dropdownS?.addEventListener('mouseover', () => {
        dropdownS?.classList.add('active');
        sublistS?.classList.add('active');
      })

      sublistS?.addEventListener('mouseout', () => {
        sublistS?.classList.remove('active');
      })
    }
  }

  if(SP.matches) {
    if(dropdownS && sublistS) {
      dropdownS?.addEventListener('click', () => {
        dropdownS?.classList.toggle('active');
        sublistS?.classList.toggle('active');
      })
    }
  }

  if(PC.matches) {
    if(dropdownP && sublistP) {
      dropdownP.forEach((item, index) => {
        item.addEventListener('mouseover', () => {
          item.classList.add('active');
          sublistP[index].classList.add('active');
          header?.classList.add('margin');
        })

        item.addEventListener('mouseout', () => {
          item.classList.remove('active');
          sublistP[index].classList.remove('active');
          header?.classList.remove('margin');
        })
      })
    }
  }

  if(SP.matches) {
    if(dropdownP && sublistP) {
      dropdownP.forEach((item, index) => {
        item.addEventListener('click', () => {
          item.classList.toggle('active');
          sublistP[index].classList.toggle('active');
          header?.classList.add('margin');
        })
      })
    }
  }

  hamburger?.addEventListener('click', () => {
    menu?.classList.toggle('active');
    hamburger.classList.toggle('close');
    document.body.classList.toggle('overflow');
    if(loginNavigation && loginHeader) {
      loginNavigation.classList.toggle('active');
      if(scrollHeader) {
        if(scrollHeader.classList.contains('active') && window.scrollY > 0) {
          loginHeader.classList.add('active');
        } else {
          loginHeader.classList.toggle('active');
        }
      }
    }
  })

  if(scrollHeader && loginHeader) {
    window.addEventListener('scroll', () => {
      if(window.scrollY > 0) {
        scrollHeader.classList.add('active')
      } else {
        scrollHeader.classList.remove('active')
      }
    })
  }
}

export default header