/**
 * Enables button when any input has been entered
 */

const enableButtonOnInput = () => {
  let inputs = document.querySelectorAll('.js-check-input input[type="text"], .js-check-input input[type="email"], textarea, input[type="hidden"]')
  let button = document.querySelector('.js-input-button')
  let hasValue

  if(inputs && button) {
    inputs.forEach(item => {
      item.addEventListener('input', () => {
        hasValue = Array.prototype.slice.call(inputs).some(x => x.value.length)

        if(button) {
          if(hasValue) {
            button.classList.add('blue')
            button.classList.remove('disabled')
          } else {
            button.classList.remove('blue')
            button.classList.add('disabled')
          }
        }
      })
    })
  }
}

export default enableButtonOnInput