/**
* Toggles the element on click
*/

const togglePopup = () => {
  const button = document.querySelectorAll('.js-popup-display')
  let hiddenElementAll = document.querySelectorAll('.js-hidden-element')

  if(button) {
    button.forEach(item => {
      item.addEventListener('click', () => {
        let hiddenElement = item.querySelector('.js-hidden-element')
        let hiddenPreviousElement = document.querySelector('.show')

        if(hiddenPreviousElement && hiddenElement !== hiddenPreviousElement) {
          hiddenPreviousElement.classList.toggle('hide')
          hiddenPreviousElement.classList.toggle('show')
        }

        if(hiddenElement) {
          hiddenElement.classList.toggle('hide')
          hiddenElement.classList.toggle('show')
        }
      })
    })

    document.body.addEventListener('click', event => {
      let eventElement = event.target as HTMLElement

      if(!(eventElement.classList.contains('js-disable-click') || eventElement.classList.contains('js-hidden-element'))) {
        hiddenElementAll.forEach(item => {
          item.classList.add('hide')
          item.classList.remove('show')
        })
      }

      if(eventElement.classList.contains('js-disable-click') || eventElement.classList.contains('js-hidden-element')) {
        eventElement.classList.add('read')
      }
    })
  }

  const popupButton = document.querySelectorAll('.js-popup-btn');
  const popupContent = document.querySelectorAll('.js-popup-content');
  const table = document.querySelector('.table__scroll');
  const PC = window.matchMedia("(min-width: 768px)");

  for(let i = 0; popupButton.length > i; i++) {
    let nearestPopup = popupButton[i].nextElementSibling

    if(nearestPopup?.classList.contains('js-popup-hover')) {
      if(PC.matches) {
        popupButton[i].addEventListener('mouseover', () => {
          if(nearestPopup) {
            nearestPopup.classList.add('popup--show');
          }
        })
  
        popupButton[i].addEventListener('mouseout', () => {
          if(nearestPopup) {
            nearestPopup.classList.remove('popup--show');
          }
        })
      }
      else {
        popupButton[i].addEventListener('click', () => {
          popupContent.forEach(item => {
            if(item.classList.contains('popup--show')) {
              if(popupContent[i] != item) {
                item.classList.remove('popup--show')
              }
            }
          })

          popupContent[i].classList.toggle('popup--show');
    
          let isPopupShow = document.querySelectorAll('popup--show')
    
          if(isPopupShow.length > 0) {
            table?.classList.remove('no-overflow');
          } else {
            table?.classList.add('no-overflow');
          }
        })
      }
    } else {
      popupButton[i].addEventListener('click', () => {
        popupContent.forEach(item => {
          if(item.classList.contains('popup--show')) {
            if(popupContent[i] != item) {
              item.classList.remove('popup--show')
            }
          }
        })

        popupContent[i].classList.toggle('popup--show');
  
        let isPopupShow = document.querySelectorAll('popup--show')
  
        if(isPopupShow.length > 0) {
          table?.classList.remove('no-overflow');
        } else {
          table?.classList.add('no-overflow');
        }
      })
    }
  }

  window.addEventListener('click', event => {
    let clickedElement = event.target as HTMLElement

    if(!clickedElement.closest('.js-popup-btn')) {
      popupContent.forEach(item => {
        item.classList.remove('popup--show')
      })
    }
  })
}

export default togglePopup
