/**
* Get the current date
*/

const getCurrentDate = () => {
  const button = document.querySelector('.js-get-current-date')
  const dateInput = document.querySelector('.js-date') as HTMLInputElement
  const hour = document.querySelector('.js-hour') as HTMLInputElement
  const minute = document.querySelector('.js-minute') as HTMLInputElement
  const select = document.querySelectorAll('.js-change-blue')
  let buttonInput = document.querySelector('.js-input-button')

  if(button && dateInput && hour && minute) {
    button.addEventListener('click', () => {
      let date = new Date()
      let currentHour = Number(date.getHours())
      let currentHourString
      let currentMinute = Number(date.getMinutes())
      let currentMinuteString

      if(currentHour < 9) {
        currentHourString = '09'
      } else {
        currentHourString = String(date.getHours())
      }

      if(currentMinute < 10) {
        currentMinuteString = '00'
      } else if(currentMinute < 20) {
        currentMinuteString = '10'
      } else if(currentMinute < 30) {
        currentMinuteString = '20'
      } else if(currentMinute < 40) {
        currentMinuteString = '30'
      } else if(currentMinute < 50) {
        currentMinuteString = '40'
      } else if(currentMinute < 60) {
        currentMinuteString = '50'
      } else {
        currentMinuteString = '59'
      }

      dateInput.value = date.getFullYear() + "/" + (date.getMonth() + 1) + "/" + date.getDate()
      hour.value = currentHourString
      minute.value = currentMinuteString

      if(select) {
        select.forEach(item => {
          item.classList.remove('light-blue')
        })
      }

      if(buttonInput) {
        buttonInput.classList.remove('disabled')
        buttonInput.classList.add('blue')
      }
    })
  }
}

export default getCurrentDate