/**
* Toggles the tab content
*/

const toggleTab = () => {
  const tabButton = document.querySelectorAll('.js-tab-button')

  if(tabButton.length > 0) {
    tabButton.forEach(item => {
      item.addEventListener('click', event => {
        event.preventDefault()

        let active = document.querySelectorAll('.active')

        if(active.length > 0) {
          active.forEach(activeItem => {
            if(activeItem.classList.contains('tab__title') || activeItem.classList.contains('tab__container')) {
              activeItem.classList.remove('active')
            }
          })
        }

        item.classList.toggle('active')

        let attributeID = item.getAttribute('href')
        if(attributeID) {
          let itemID = document.querySelector(attributeID)
          
          if(itemID) {
            itemID.classList.add('active')
          }
        }
      })
    })
  }
}

export default toggleTab
