/**
* Add pagination functionality
*/

const pagination = () => {
  const pagination = document.querySelectorAll('.pagination__input')
  const previous = document.querySelectorAll('.pagination__nextprev--previous')
  const next = document.querySelectorAll('.pagination__nextprev:last-child')

  if(pagination.length > 0) {
    pagination.forEach(item => {
      item.addEventListener('input', () => {
        let itemElement = item as HTMLInputElement

        pagination.forEach((subitem, index) => {
          let subitemElement = subitem as HTMLInputElement

          subitemElement.value = itemElement.value

          if(Number(itemElement.value) > 1) {
            previous[index].classList.remove('disabled')
          } else {
            previous[index].classList.add('disabled')
          }

          if(Number(itemElement.value) >= 999) {
            next[index].classList.add('disabled')
          } else {
            next[index].classList.remove('disabled')
          }
        })
      })
    })
  }
}

export default pagination
