/**
* Adds and delete candidate names
*/
function setInputFilter(textbox: Element, inputFilter: (value: string) => boolean): void {
  ['input', 'keydown', 'keyup', 'mousedown', 'mouseup', 'select', 'contextmenu', 'drop'].forEach(function(event) {
    textbox.addEventListener(event, function(this: (HTMLInputElement | HTMLTextAreaElement) & {oldValue: string; oldSelectionStart: number | null, oldSelectionEnd: number | null}) {
      if (inputFilter(this.value)) {
        this.oldValue = this.value
        this.oldSelectionStart = this.selectionStart
        this.oldSelectionEnd = this.selectionEnd
      } else if (Object.prototype.hasOwnProperty.call(this, 'oldValue')) {
        this.value = this.oldValue
        if (this.oldSelectionStart !== null &&
          this.oldSelectionEnd !== null) {
          this.setSelectionRange(this.oldSelectionStart, this.oldSelectionEnd)
        }
      } else {
        this.value = ''
      }
    })
  })
}

const addCandidate = () => {
  let subcontent = document.querySelectorAll('.js-subcontent')

  if(subcontent.length > 0) {
    subcontent.forEach(subcontentItem => {
      subcontentItem.addEventListener('click', event => {
        let clickedElement = event.target as HTMLElement

        if(clickedElement.classList.contains('js-add-proposal')) {
          const addButton = clickedElement
          let parentElement = clickedElement.closest('.js-clone-content')

          if(parentElement) {
            const content = parentElement.querySelector('.js-candidate-forms')
            const subitem = parentElement.querySelector('.js-subitem')
            let label = parentElement.querySelectorAll('.js-candidate-label')
            let secondLabel = parentElement.querySelectorAll('.js-second-candidate-label')
            const html = String(subitem?.innerHTML)

            if(addButton) {
              let clone = document.createElement('div')

              if(clone && content) {
                clone.classList.add('form__subitem')
                clone.classList.add('js-subitem')
                clone.innerHTML = html

                let deleteClone = clone.querySelector('.js-delete-content')
                let inputNumber = clone.querySelectorAll('.js-input-number')

                if(inputNumber.length > 0) {
                  inputNumber.forEach(item => {
                    let itemElement = item as HTMLInputElement
                    itemElement.type = 'text'
                    setInputFilter(item, value => {
                      return /^[0-9]*$/.test(value)
                    })
                  })
                }

                let inputs = clone.querySelectorAll('input, textarea')

                inputs.forEach(inputItem => {
                  let inputElement = inputItem as HTMLInputElement
                  if(!(inputItem.getAttribute('type') === 'radio')) {
                    inputElement.value = ''
                  }
                  inputItem.classList.remove('has-name')
                  inputItem.classList.add('flexible-name')
                })

                if(deleteClone) {
                  deleteClone.classList.remove('hide')

                  deleteClone.addEventListener('click', () => {
                    if(clone) {
                      clone.remove()
                    }

                    if(parentElement) {
                      label = parentElement.querySelectorAll('.js-candidate-label')
                      secondLabel = parentElement.querySelectorAll('.js-second-candidate-label')
            
                      label.forEach((labelItem, labelIndex) => {
                        labelItem.innerHTML = '' + (labelIndex + 1)
                        secondLabel[labelIndex].innerHTML = '' + (labelIndex + 1)
                      })
                    }
                  })
                }

                content.appendChild(clone)
              }

              if(parentElement && content) {
                label = parentElement.querySelectorAll('.js-candidate-label')
                secondLabel = parentElement.querySelectorAll('.js-second-candidate-label')

                label.forEach((labelItem, labelIndex) => {
                  labelItem.innerHTML = '' + (labelIndex + 1)
                  secondLabel[labelIndex].innerHTML = '' + (labelIndex + 1)
                })
              }
            }
          }
        }
      })
    })
  }
}

export default addCandidate