/**
 * Toggles the label in modal
 */

const toggleLabel = () => {
  const label = document.querySelectorAll('.js-toggle')

  if(label.length > 0) {
    label.forEach(item => {
      item.addEventListener('click', e => {
        let elementClicked = e.target as HTMLElement
        
        if(!elementClicked.parentElement?.classList.contains('checkbox')) {
          let parentElement = item.parentElement
          if(parentElement) {
            parentElement.classList.toggle('active')
          }

          let content = item.nextElementSibling as HTMLElement
          if(content) {
            if (content.style.maxHeight) {
              content.style.maxHeight = ''
            } else {
              content.style.maxHeight = content.scrollHeight + 'px'
            }
          }
        }
      })

      window.addEventListener('resize', () => {
        label.forEach(item => {
          let content = item.nextElementSibling as HTMLElement
          if(content) {
            if (content.style.maxHeight) {
              content.style.maxHeight = ''
            } else {
              content.style.maxHeight = content.scrollHeight + 'px'
            }
          }
        })
      })
    })
  }
}

export default toggleLabel
