/**
 * Add scroll arrow to overflow
 */

 const scrollArrow = () => {
  const scrollContainer = document.querySelectorAll('.js-scroll-wrapper')

  if(scrollContainer) {
    scrollContainer.forEach(item => {
      let parentElement = item.parentElement
      if(parentElement) {
        let leftArrowItem = parentElement.querySelector('.js-arrow-left') as HTMLElement
        let rightArrowItem = parentElement.querySelector('.js-arrow-right') as HTMLElement
        let topArrowItem = parentElement.querySelector('.js-arrow-top') as HTMLElement
        let bottomArrowItem = parentElement.querySelector('.js-arrow-bottom') as HTMLElement

        if(leftArrowItem && rightArrowItem) {
          if(item.scrollWidth <= item.clientWidth) {
            leftArrowItem.style.display = 'none'
            rightArrowItem.style.display = 'none'
          }
  
          leftArrowItem.addEventListener('click', () => {
            item.scrollLeft -= 50
          })
    
          rightArrowItem.addEventListener('click', () => {
            item.scrollLeft += 50
          })
        }
  
        if(topArrowItem && bottomArrowItem) {
          if(item.scrollHeight <= item.clientHeight) {
            topArrowItem.style.display = 'none'
            bottomArrowItem.style.display = 'none'
          }
  
          topArrowItem.addEventListener('click', () => {
            item.scrollTop -= 50
          })
    
          bottomArrowItem.addEventListener('click', () => {
            item.scrollTop += 50
          })
        }
      }
    })
  }
}

export default scrollArrow