/**
 * Toggles the folder
 */

 const toggleFolder = () => {
  const folder = document.querySelectorAll('.js-toggle-folder')
  const fileContainer = document.querySelectorAll('.js-toggle-files')

  if(folder && fileContainer) {
    folder.forEach((item, index) => {
      item.addEventListener('click', () => {
        item.classList.toggle('active')
        fileContainer[index].classList.toggle('active')
      })
    })
  }
}

export default toggleFolder
