/**
 * Intializes SortableJS
 */

import toggleCheckbox from "./toggleCheckbox";

const sortable = () => {
  let nestedSortables = [].slice.call(document.querySelectorAll('.js-target-draggable'));
  var draggedDepth = 0;
  let beforeElement : any

  if(nestedSortables.length > 0 || nestedSortables) {
    for (var i = 0; i < nestedSortables.length; i++) {
      //@ts-ignore
      new Sortable(nestedSortables[i], {
        forceFallback: true,
        multiDrag: true,
        selectedClass: 'selected',
        fallbackTolerance: 3,
        fallbackClass: 'ui-sortable-helper',
        ghostClass: 'ui-ghost',
        avoidImplicitDeselect: true,
        handle: '.js-draggable',
        group: {
          name: 'nested',
          pull: (to : any, from : any) => {
            //@ts-ignore
            var toLvl = $(to.el).parents('.js-target-draggable').length;
            if(toLvl > 1) {
              return false;
            }
            if(draggedDepth > 0) {
              return false;
            }
  
            return true;
          }
        },
        animation: 150,
        fallbackOnBody: true,
        swapThreshold: 0.7,
        onStart: () => {
          //@ts-ignore
          Sortable.ghost.style.opacity = 1;
        },
        emptyInsertThreshold: 8,
        onMove: (evt : any, originalEvent : any) => {
          //@ts-ignore
          draggedDepth = $(evt.dragged).find('.js-target-draggable > *').length
  
          let parentElement = evt.to
  
          if(parentElement.classList.contains('list__subcontent')) {
            beforeElement = parentElement.closest('.list__block')
            parentElement.closest('.list__block').classList.add('ui-folder-container')
          } else if(beforeElement) {
            beforeElement.classList.remove('ui-folder-container')
          }
        },
        onSelect: (event : any) => {
          let checkbox = event.item.querySelector('.js-checkbox-activate input')
  
          setTimeout(() => {
            if(!checkbox.checked) {
              //@ts-ignore
              Sortable.utils.deselect(event.item)
            }
          }, 1)
        },
        onEnd: (event: any) => {
          toggleCheckbox()
  
          let folder = event.item.closest('.list__block')
  
          if(folder) {
            folder.classList.remove('ui-folder-container')
          }
        }
      });
    }
  }
}

export default sortable