/**
 * Create functionality for two side multiselect
 */

 const swapElements = (obj1 : any, obj2 : any) => {
  let parent2 = obj2.parentNode
  let next2 = obj2.nextElementSibling

  if (next2 === obj1) {
    parent2.insertBefore(obj1, obj2)
  } else {
    obj1.parentNode.insertBefore(obj2, obj1)

    if (next2) {
      parent2.insertBefore(obj1, next2)
    } else {
      parent2.appendChild(obj1)
    }
  }
}

const twoSideMultiSelect = () => {
  const source = document.querySelector('.js-multiselect-source') as HTMLSelectElement
  const target = document.querySelector('.js-multiselect-target') as HTMLSelectElement
  const right = document.querySelector('.js-multiselect-right')
  const left = document.querySelector('.js-multiselect-left')
  const rightAll = document.querySelector('.js-multiselect-right-all')
  const leftAll = document.querySelector('.js-multiselect-left-all')
  const inputHidden = document.querySelector('.js-target-value') as HTMLInputElement
  let button = document.querySelector('.js-input-button')
  let draggable : String

  if(right && left && source && target) {
    right.addEventListener('click', () => {
      let sourceValue = source.querySelectorAll('.multiselect')

      if(sourceValue.length > 0) {
        sourceValue.forEach(item => {
          let inputElement = item.querySelectorAll('input')

          if(inputElement) {
            inputElement.forEach(inputItem => {
              if(inputItem.checked) {
                item.remove()

                if(target.classList.contains('js-target-draggable')) {
                  draggable = '<div class="draggable js-draggable"><div></div><div></div><div></div>'
                }

                item.classList.remove('selected')

                target.innerHTML += `<label class="${item.className}">${item.innerHTML}${draggable}</label>`

                if(inputHidden) {
                  inputHidden.value = 'hasValue'
                  if(button) {
                    button.classList.add('blue')
                    button.classList.remove('disabled')
                  }
                }
              }
            })
          }
        })
      }
    })

    if(rightAll) {
      rightAll.addEventListener('click', () => {
        let sourceValue = source.querySelectorAll('.multiselect')

        if(sourceValue.length > 0) {
          sourceValue.forEach(item => {
            let inputElement = item.querySelectorAll('input')

            if(inputElement) {
              inputElement.forEach(inputItem => {
                inputItem.checked = true
              })

              inputElement.forEach(inputItem => {
                if(inputItem.checked) {
                  item.remove()

                  if(target.classList.contains('js-target-draggable')) {
                    draggable = '<div class="draggable js-draggable"><div></div><div></div><div></div>'
                  }

                  item.classList.remove('selected')

                  target.innerHTML += `<label class="${item.className}">${item.innerHTML}${draggable}</label>`
                }
              })
            }
          })
        }
      })
    }

    left.addEventListener('click', () => {
      let targetValue = target.querySelectorAll('.multiselect')

      if(targetValue.length > 0) {
        targetValue.forEach(item => {
          let inputElement = item.querySelectorAll('input')

          if(inputElement) {
            inputElement.forEach(inputItem => {
              if(inputItem.checked) {
                item.remove()

                item.classList.remove('selected')

                source.innerHTML += `<label class="${item.className}">${item.innerHTML}</label>`

                let innerLabel = target.querySelectorAll('label')

                if(innerLabel.length <= 0 && inputHidden) {
                  inputHidden.value = ''

                  let inputs = document.querySelectorAll('.js-check-input input[type="text"], .js-check-input input[type="email"], textarea')
                  if(inputs && button) {
                    let hasValue = Array.prototype.slice.call(inputs).some(x => x.value.length)

                    if(button) {
                      if(hasValue) {
                        button.classList.add('blue')
                        button.classList.remove('disabled')
                      } else {
                        button.classList.remove('blue')
                        button.classList.add('disabled')
                      }
                    }
                  }
                }
              }
            })
          }
        })
      }
    })

    if(leftAll) {
      leftAll.addEventListener('click', () => {
        let targetValue = target.querySelectorAll('.multiselect')

        if(targetValue.length > 0) {
          targetValue.forEach(item => {
            let inputElement = item.querySelectorAll('input')

            if(inputElement) {
              inputElement.forEach(inputItem => {
                inputItem.checked = true
              })

              inputElement.forEach(inputItem => {
                if(inputItem.checked) {
                  item.remove()

                  item.classList.remove('selected')

                  source.innerHTML += `<label class="${item.className}">${item.innerHTML}</label>`
                }
              })
            }
          })
        }
      })
    }
  }
}

export default twoSideMultiSelect