/**
 * Enables unchecking of radio button
 */

const enableUncheckingRadioButton = () => {
  let radio = document.querySelectorAll('.js-enable-uncheck input')
  let previousState = false
  let previousElement : any

  if(radio) {
    radio.forEach(item => {
      item.addEventListener('click', () => {
        let element = item as HTMLInputElement

        if((previousElement === element) && previousState) {
          element.checked = false
        }

        previousElement = element
        previousState = element.checked
      })
    })
  }
}

export default enableUncheckingRadioButton