/**
* Graph Button Hover
*/

const graphButton = () => {
  const graphButton = document.querySelector('.js-graph-hover');
  const graphWrapper = document.querySelector('.js-graph-wrapper');
  const SP = window.matchMedia("(max-width: 767px)");
  const PC = window.matchMedia("(min-width: 768px)");

  if(PC.matches) {
    if(graphButton && graphWrapper) {
      graphButton?.addEventListener('mouseover', () => {
        graphWrapper?.classList.add('show');
    
        graphWrapper?.addEventListener('mouseover', () => {
          graphWrapper?.classList.add('show');
        })
    
        graphWrapper?.addEventListener('mouseout', () => {
          graphWrapper?.classList.remove('show');
        })
      })
    
      graphButton?.addEventListener('mouseout', () => {
        graphWrapper?.classList.remove('show');
      })
    }
  } else if(SP.matches) {
    if(graphButton && graphWrapper) {
      graphButton?.addEventListener('click', () => {
        graphWrapper?.classList.toggle('show');
      })
    }
  }
}

export default graphButton
