/**
* Toggles the on click functionlity on SHIFT keypress
*/

const toggleOnClickShift = () => {
  let label = document.querySelectorAll('.js-shift-key label')

  if(window.matchMedia("(pointer: coarse)").matches) {
    if(label) {
      label.forEach(item => {
        item.classList.remove('disabled')
      })
    }
  }

  document.addEventListener('keydown', event => {
    label = document.querySelectorAll('.js-shift-key label')
  
    if(event.key === 'Shift') {
      if(label) {
        label.forEach(item => {
          item.classList.remove('disabled')
        })
      }
    }
  })

  document.addEventListener('keyup', event => {
    label = document.querySelectorAll('.js-shift-key label')
  
    if(label) {
      label.forEach(item => {
        item.classList.add('disabled')
      })
    }
  })
}

export default toggleOnClickShift
