/**
* Edit Button Hover
*/

const editButton = () => {
  const editButton = document.querySelectorAll('.js-button-hover');
  const editButtonClick = document.querySelectorAll('.js-button-click');
  const buttonWrapper = document.querySelectorAll('.js-button-wrapper');
  const tableRow = document.querySelectorAll('.js-table-row');
  const SP = window.matchMedia("(max-width: 767px)");
  const PC = window.matchMedia("(min-width: 768px)");
  let currentButton

  if(editButton.length > 0) {
    currentButton = editButton
  } else {
    currentButton = editButtonClick
  }

  if(PC.matches) {
    if(currentButton && buttonWrapper) {
      for(let x = 0; x < currentButton.length; x++) {
        let buttonWrapperItem = buttonWrapper[x] as HTMLElement
        let tableRowItem = tableRow[x] as HTMLElement
        let editButtonItem = currentButton[x] as HTMLElement

        let bodyRect = tableRowItem.getBoundingClientRect()
        let elemRect = currentButton[x].getBoundingClientRect()
        let offset   = elemRect.top - bodyRect.top

        buttonWrapperItem.style.bottom = (offset + editButtonItem.offsetHeight) + 'px';

        if(editButton.length > 0) {
          editButton[x].addEventListener('mouseover', () => {
            buttonWrapper[x].classList.add('active');
          })
    
          editButton[x].addEventListener('mouseout', () => {
            buttonWrapper[x].classList.remove('active');
          })
        } else {
          editButtonClick[x].addEventListener('click', () => {
            buttonWrapper.forEach(item => {
              if(item.classList.contains('active')) {
                if(buttonWrapper[x] != item) {
                  item.classList.remove('active')
                }
              }
            })

            buttonWrapper[x].classList.toggle('active');
          })
        }
      }
    }
  } else if(SP.matches) {
    if(currentButton && buttonWrapper) {
      for(let x = 0; x < currentButton.length; x++) {
        let buttonWrapperItem = buttonWrapper[x] as HTMLElement
        let tableRowItem = tableRow[x] as HTMLElement
        let editButtonItem = currentButton[x] as HTMLElement

        if(editButton[x]) {
          editButton[x].addEventListener('click', () => {
            let tableRowElement = tableRow[x] as HTMLElement
            buttonWrapper[x].classList.toggle('active');
            if(buttonWrapper[x].classList.contains('active')) {
              tableRowElement.style.paddingTop = (buttonWrapperItem.offsetHeight) + 'px';
            } else if(buttonWrapper[x].classList.contains('no-sp-padding')) {
              tableRowElement.style.paddingTop = '0';
            } else {
              tableRowElement.style.paddingTop = '10px';
            }
  
            let bodyRect = tableRowItem.getBoundingClientRect()
            let elemRect = editButton[x].getBoundingClientRect()
            let offset   = (elemRect.top - bodyRect.top) - buttonWrapperItem.offsetHeight
  
            buttonWrapperItem.style.bottom = (offset + (editButtonItem.offsetHeight + 15)) + 'px';
          })
        } else {
          editButtonClick[x].addEventListener('click', () => {
            buttonWrapper.forEach(item => {
              if(item.classList.contains('active')) {
                if(buttonWrapper[x] != item) {
                  let tableRowElementInner = item.closest('.js-table-row') as HTMLElement
                  item.classList.remove('active')

                  if(buttonWrapper[x].classList.contains('active')) {
                    tableRowElementInner.style.paddingTop = (buttonWrapperItem.offsetHeight) + 'px';
                  } else if(buttonWrapper[x].classList.contains('no-sp-padding')) {
                    tableRowElementInner.style.paddingTop = '0';
                  } else {
                    tableRowElementInner.style.paddingTop = '10px';
                  }
                }
              }
            })

            let tableRowElement = tableRow[x] as HTMLElement
            buttonWrapper[x].classList.toggle('active');
            if(buttonWrapper[x].classList.contains('active')) {
              tableRowElement.style.paddingTop = (buttonWrapperItem.offsetHeight) + 'px';
            } else if(buttonWrapper[x].classList.contains('no-sp-padding')) {
              tableRowElement.style.paddingTop = '0';
            } else {
              tableRowElement.style.paddingTop = '10px';
            }
  
            let bodyRect = tableRowItem.getBoundingClientRect()
            let elemRect = editButtonClick[x].getBoundingClientRect()
            let offset   = (elemRect.top - bodyRect.top) - buttonWrapperItem.offsetHeight
  
            buttonWrapperItem.style.bottom = (offset + (editButtonItem.offsetHeight + 15)) + 'px';
          })
        }
      }
    }
  }

  window.addEventListener('click', event => {
    let clickedElement = event.target as HTMLElement

    if(!clickedElement.closest('.js-button-click')) {
      buttonWrapper.forEach((item, index) => {
        let buttonWrapperItem = item as HTMLElement
        item.classList.remove('active')

        let tableRowElement = tableRow[index] as HTMLElement
  
        if(item.classList.contains('active')) {
          tableRowElement.style.paddingTop = (buttonWrapperItem.offsetHeight) + 'px';
        } else if(item.classList.contains('no-sp-padding')) {
          tableRowElement.style.paddingTop = '0';
        } else {
          tableRowElement.style.paddingTop = '10px';
        }
      })
    }
  })

  const resetBtn = document.querySelector('.js-reset');
  const resetWrap = document.querySelector('.js-select-wrap');
  const resetOption = document.querySelector('.js-select-option');

  resetBtn?.addEventListener('click', () => {
    resetWrap?.classList.add('select--gray');

    resetWrap?.addEventListener('click', () => {
      resetWrap?.classList.remove('select--gray');
    })
  })

  const textGray = document.querySelectorAll('.js-gray');

  for(let x = 0; x < textGray.length; x++) {
    textGray[x].addEventListener('click', () => {
      textGray[x].classList.add('dark-gray');
    })
  }
}

export default editButton
