/**
* Adds flexible content ui functionality
*/

function setInputFilter(textbox: Element, inputFilter: (value: string) => boolean): void {
  ['input', 'keydown', 'keyup', 'mousedown', 'mouseup', 'select', 'contextmenu', 'drop'].forEach(function(event) {
    textbox.addEventListener(event, function(this: (HTMLInputElement | HTMLTextAreaElement) & {oldValue: string; oldSelectionStart: number | null, oldSelectionEnd: number | null}) {
      if (inputFilter(this.value)) {
        this.oldValue = this.value
        this.oldSelectionStart = this.selectionStart
        this.oldSelectionEnd = this.selectionEnd
      } else if (Object.prototype.hasOwnProperty.call(this, 'oldValue')) {
        this.value = this.oldValue
        if (this.oldSelectionStart !== null &&
          this.oldSelectionEnd !== null) {
          this.setSelectionRange(this.oldSelectionStart, this.oldSelectionEnd)
        }
      } else {
        this.value = ''
      }
    })
  })
}

const getDuplicateContent = (subcontent : any, content: any, labelString : string, noParenthesis? : boolean, secondLabelString? : string) => {
  if(subcontent.length > 0) {
    subcontent.forEach((subcontentItem : any) => {
      const addButton = subcontentItem.querySelector('.js-add-ss')
      let label = subcontentItem.querySelectorAll('.js-label-ss')
      let secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')
      let deleteButton = subcontentItem.querySelectorAll('.js-delete-ss')

      if(deleteButton.length > 0) {
        deleteButton.forEach((deleteItem : any) => {
          deleteItem.addEventListener('click', () => {
            let deleteParent = deleteItem.closest('.js-duplicate')

            if(deleteParent) {
              deleteParent.remove()
            }
  
            label = subcontentItem.querySelectorAll('.js-label-ss')
            secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')
  
            label.forEach((labelItem : any, labelIndex : any) => {
              if(noParenthesis) {
                labelItem.innerHTML = labelString + (labelIndex + 1)
              } else {
                labelItem.innerHTML = labelString + '(' + (labelIndex + 1) + ')'
              }
              if(secondLabel[labelIndex]) {
                secondLabel[labelIndex].innerHTML = secondLabelString + '(' + (labelIndex + 1) + ')'
              }
            })
          })
        })
      }

      if(addButton) {
        addButton.addEventListener('click', () => {
          let html = addButton.parentElement.previousElementSibling.querySelector('.js-duplicate').innerHTML

          let clone = document.createElement('div')
          
          if(clone && content) {
            clone.classList.add('form__select-duplicate')
            clone.classList.add('js-duplicate')
            clone.innerHTML = html

            let deleteClone = clone.querySelector('.js-delete-ss')
            let inputNumber = clone.querySelectorAll('.js-input-number')
            let inputName = clone.querySelectorAll('.has-name')
            let inputs = clone.querySelectorAll('input, textarea')

            if(inputNumber.length > 0) {
              inputNumber.forEach(item => {
                let itemElement = item as HTMLInputElement
                itemElement.type = 'text'
                setInputFilter(item, value => {
                  return /^[0-9]*$/.test(value)
                })
              })
            }

            inputs.forEach((inputItem : any) => {
              if(!(inputItem.getAttribute('type') === 'radio')) {
                inputItem.value = ''
              }
            })

            if(inputName.length > 0) {
              inputName.forEach(item => {
                item.classList.remove('has-name')
                item.classList.add('has-subname')
              })
            }

            if(deleteClone) {
              deleteClone.classList.remove('hide')

              deleteClone.addEventListener('click', () => {
                if(clone) {
                  clone.remove()
                }

                label = subcontentItem.querySelectorAll('.js-label-ss')
                secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')

                label.forEach((labelItem : any, labelIndex : any) => {
                  if(noParenthesis) {
                    labelItem.innerHTML = labelString + (labelIndex + 1)
                  } else {
                    labelItem.innerHTML = labelString + '(' + (labelIndex + 1) + ')'
                  }
                  if(secondLabel[labelIndex]) {
                    secondLabel[labelIndex].innerHTML = secondLabelString + '(' + (labelIndex + 1) + ')'
                  }
                })
              })
            }

            content.appendChild(clone)
          }

          label = subcontentItem.querySelectorAll('.js-label-ss')
          secondLabel = subcontentItem.querySelectorAll('.js-label-second-ss')

          label.forEach((labelItem : any, labelIndex : any) => {
            if(noParenthesis) {
              labelItem.innerHTML = labelString + (labelIndex + 1)
            } else {
              labelItem.innerHTML = labelString + '(' + (labelIndex + 1) + ')'
            }
            if(secondLabel[labelIndex]) {
              secondLabel[labelIndex].innerHTML = secondLabelString + '(' + (labelIndex + 1) + ')'
            }
          })
        })
      }
    })
  }
}

const getFlexibleContent = (subcontent : any, isBill? : boolean, mainLabel? : string) => {
  let indexInput : any = []

  if(subcontent.length > 0) {
    subcontent.forEach((subcontentItem : any, subcontentIndex : number) => {
      let cloneParent = subcontentItem.querySelector('.js-clone-parent')
      const addButton = subcontentItem.querySelector('.js-add-content')
      const content = subcontentItem.querySelector('.js-clone-content')
      let deleteButton = subcontentItem.querySelectorAll('.js-delete-content')
      let label = subcontentItem.querySelectorAll('.js-label')


      indexInput[subcontentIndex] = 1
      let html = content.innerHTML
      let inputClones : any = []

      if(addButton && content && deleteButton) {
        addButton.addEventListener('click', () => {
          let clone = document.createElement('div')

          if(clone && cloneParent) {
            clone.classList.add('subcontent__draggable')
            clone.classList.add('js-clone-content')
            clone.innerHTML = html

            let subitem = clone.querySelectorAll('.js-subitem')
            let formSelectContainer = clone.querySelectorAll('.form__select-container')
            let select = clone.querySelectorAll('select')
            let show = clone.querySelectorAll('.show')

            if(subitem.length > 1) {
              subitem.forEach((item, index) => {
                if(index > 0) {
                  item.remove()
                }
              })
            }

            if(formSelectContainer.length > 0) {
              formSelectContainer.forEach(item => {
                let jsDuplicate = item.querySelectorAll('.js-duplicate')

                if(jsDuplicate.length > 1) {
                  jsDuplicate.forEach((duplicateItem, index) => {
                    if(index > 0) {
                      duplicateItem.remove()
                    }
                  })
                }
              })
            }

            if(select.length > 0) {
              select.forEach(item => {
                let selectElement = item as HTMLSelectElement
                selectElement.selectedIndex = 0
                selectElement.classList.add('gray')

                item.addEventListener('change', () => {
                  selectElement.classList.remove('gray')
                })
              })
            }

            if(show.length > 0) {
              show.forEach(item => {
                item.classList.add('hide')
                item.classList.remove('show')
              })
            }

            let question = clone.querySelectorAll('.question')

            if(question.length > 0) {
              question.forEach(questionItem => {
                questionItem.classList.add('hide')
              })
            }
    
            let inputs = clone.querySelectorAll('input, textarea')
            let deleteClone = clone.querySelector('.js-delete-content')
            let autoResize = clone.querySelectorAll('.js-auto-resize')
            let inputNumber = clone.querySelectorAll('.js-input-number')
            indexInput[subcontentIndex] = indexInput[subcontentIndex] + 1
    
            const radioClone = clone.querySelectorAll('.js-candidate-toggle')
            const contentClone = clone.querySelector('.js-candidate-content')

            let subcontentSelection = clone.querySelectorAll('.js-single-selection')
            let contentSelection = clone.querySelector('.js-content-ss')

            getDuplicateContent(subcontentSelection, contentSelection, ' ', false, ' ')


            let subcontentMultipleSelection = clone.querySelectorAll('.js-multiple-selection')
            let contentMultipleSelection = clone.querySelector('.js-content-ms')

            getDuplicateContent(subcontentMultipleSelection, contentMultipleSelection, ' ')

            let subcontentSingleFreeInput = clone.querySelectorAll('.js-single-selection-free-input')
            let contentSingleFreeInput = clone.querySelector('.js-content-ssfi')

            getDuplicateContent(subcontentSingleFreeInput, contentSingleFreeInput, ' ', false, ' ')

            let subcontentMultipleFreeInput = clone.querySelectorAll('.js-multiple-selection-free-input')
            let contentMultipleFreeInput = clone.querySelector('.js-content-msfi')

            getDuplicateContent(subcontentMultipleFreeInput, contentMultipleFreeInput, ' ')

            let subcontentMatrixSingleSelection = clone.querySelectorAll('.js-matrix-single-selection')
            let contentMatrixSingleSelection = clone.querySelector('.js-content-mss')

            getDuplicateContent(subcontentMatrixSingleSelection, contentMatrixSingleSelection, ' ')

            let subcontentSingleSelectionChild = clone.querySelectorAll('.js-single-selection-child')
            let contentSingleSelectionChild = clone.querySelector('.js-content-ssc')

            getDuplicateContent(subcontentSingleSelectionChild, contentSingleSelectionChild, ' ')

            if(autoResize.length > 0) {
              autoResize.forEach(item => {
                let itemElement = item as HTMLInputElement
                item.addEventListener('input', () => {
                  itemElement.style.height = window.getComputedStyle(item).getPropertyValue('min-height')
                })
              })
            }

            if(inputNumber.length > 0) {
              inputNumber.forEach(item => {
                let itemElement = item as HTMLInputElement
                itemElement.type = 'text'
                setInputFilter(item, value => {
                  return /^[0-9]*$/.test(value)
                })
              })
            }

            let index = 0

            inputs.forEach((inputItem : any) => {
              if(!(inputItem.getAttribute('type') === 'radio')) {
                inputItem.value = ''
              }

              if(inputItem.name) {
                inputClones[index] = inputItem.name
                inputItem.classList.add('has-name')
                inputItem.name = ''
                index = index + 1
              }
            })
    
            if(deleteClone) {
              deleteClone.addEventListener('click', () => {
                if(clone) {
                  clone.remove()
                }
      
                label = subcontentItem.querySelectorAll('.js-label')
      
                label.forEach((labelItem : any, labelIndex : number) => {
                  if(mainLabel) {
                    labelItem.innerHTML = mainLabel + (labelIndex + 1)
                  } else {
                    labelItem.innerHTML = '' + (labelIndex + 1) + ''
                  }
                })

                let mainParent = subcontentItem.querySelectorAll('.js-clone-content')

                mainParent.forEach((cloneItem : any, cloneIndex : number) => {
                  let inputs = cloneItem.querySelectorAll('.has-name')

                  inputs.forEach((inputItem : any, inputIndex : number) => {
                    inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                  })

                  let inputFlexibleName = cloneItem.querySelectorAll('.flexible-name')

                  inputFlexibleName.forEach((inputItem : any) => {
                    inputItem.name = inputItem.name.replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                  })
                })
              })
            }
    
            if(radioClone && contentClone) {
              radioClone.forEach(item => {
                item.addEventListener('click', () => {
                  let itemElement = item as HTMLInputElement
                  if(itemElement.value === '') {
                    contentClone.classList.add('active')
                  } else {
                    contentClone.classList.remove('active')
                  }
                })
              })
            }

            cloneParent.appendChild(clone)
          }
    
          label = subcontentItem.querySelectorAll('.js-label')

          const duplicateContent = subcontentItem.querySelectorAll('.js-duplicate-content')
          let limit = subcontentItem.dataset.limit
    
          label.forEach((labelItem : any, labelIndex : number) => {
            if(mainLabel) {
              labelItem.innerHTML = mainLabel + (labelIndex + 1)
            } else {
              labelItem.innerHTML = '' + (labelIndex + 1) + ''
            }

            if((labelIndex + 1) >= limit) {
              addButton.classList.add('hide')
            } else {
              addButton.classList.remove('hide')
            }

            duplicateContent.forEach((duplicateItem : any) => {
              if((labelIndex + 1) >= limit) {
                duplicateItem.classList.add('hide')
              } else {
                duplicateItem.classList.remove('hide')
              }
            })
          })

          let mainParent = subcontentItem.querySelectorAll('.js-clone-content')

          mainParent.forEach((cloneItem : any, cloneIndex : number) => {
            let inputs = cloneItem.querySelectorAll('.has-name')

            inputs.forEach((inputItem : any, inputIndex : number) => {
              inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
            })
          })
        })
    
        if(deleteButton.length > 0) {
          deleteButton.forEach((deleteItem : any) => {
            deleteItem.addEventListener('click', () => {
              let parent = deleteItem.closest('.js-subitem')
              let mainParent = deleteItem.closest('.js-candidate-forms')

              if(!mainParent) {
                mainParent = deleteItem.closest('.form__select-content')
              }

              if(parent) {
                parent.remove()

                let subLabel = mainParent.querySelectorAll('.js-candidate-label')
                let secondLabel = mainParent.querySelectorAll('.js-second-candidate-label')

                subLabel.forEach((labelItem : any, labelIndex : any) => {
                  labelItem.innerHTML = '' + (labelIndex + 1)
                  secondLabel[labelIndex].innerHTML = '' + (labelIndex + 1)
                })
              } else {
                parent = deleteItem.closest('.js-clone-content')

                parent.remove()

                label = subcontentItem.querySelectorAll('.js-label')
        
                label.forEach((labelItem : any, labelIndex : number) => {
                  if(mainLabel) {
                    labelItem.innerHTML = mainLabel + (labelIndex + 1)
                  } else {
                    labelItem.innerHTML = '' + (labelIndex + 1) + ''
                  }
                })

                let mainParent = subcontentItem.querySelectorAll('.js-clone-content')
      
                mainParent.forEach((cloneItem : any, cloneIndex : number) => {
                  let inputs = cloneItem.querySelectorAll('.has-name')
      
                  inputs.forEach((inputItem : any, inputIndex : number) => {
                    inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                  })
                })
              }
            })
          })
        }
      }

      const parentDuplicated = subcontentItem.querySelector('.js-clone-parent')
      inputClones = []

      if(parentDuplicated) {
        parentDuplicated.addEventListener('click', (event : any) => {
          let clickedElement = event.target as HTMLElement

          if(clickedElement) {
            if(clickedElement.getAttribute('type') === 'radio') {
              let checkedElement = clickedElement.closest('.form__input-subwrap')?.querySelector('.js-checked')
              checkedElement?.classList.remove('js-checked')
              clickedElement.classList.add('js-checked')
            }

            if(clickedElement.classList.contains('js-duplicate-content')) {
              let parentClickedElement = clickedElement.closest('.js-clone-content')

              if(parentClickedElement) {
                let clone:any = parentClickedElement.cloneNode(true)
                let inputs = clone.querySelectorAll('.has-name')
                let deleteClone = clone.querySelector('.js-delete-content')
                let autoResize = clone.querySelectorAll('.js-auto-resize')
                let questions = clone.querySelectorAll('.question')
                let candidateForm = clone.querySelector('.js-candidate-forms')
                let deleteButton
                let selectParent = parentClickedElement.querySelectorAll('select')
                let selectChild = clone.querySelectorAll('select')

                if(selectParent.length > 0) {
                  selectParent.forEach((selectItem : any, selectIndex : number) => {
                    let selectElement = selectItem as HTMLSelectElement
                    selectChild[selectIndex].value = selectElement.value
                  })
                }

                if(candidateForm) {
                  deleteButton = candidateForm.querySelectorAll('.js-delete-content')
                } else {
                  deleteButton = clone.querySelectorAll('.js-delete-ss')
                }

                const inputNumberDuplicate = clone.querySelectorAll('.js-input-number')

                const radioClone = clone.querySelectorAll('.js-candidate-toggle')
                const contentClone = clone.querySelector('.js-candidate-content')

                let subcontentSelection = clone.querySelectorAll('.js-single-selection')
                let contentSelection = clone.querySelector('.js-content-ss')
    
                getDuplicateContent(subcontentSelection, contentSelection, ' ', false, ' ')
    
    
                let subcontentMultipleSelection = clone.querySelectorAll('.js-multiple-selection')
                let contentMultipleSelection = clone.querySelector('.js-content-ms')
    
                getDuplicateContent(subcontentMultipleSelection, contentMultipleSelection, ' ')

                let subcontentSingleFreeInput = clone.querySelectorAll('.js-single-selection-free-input')
                let contentSingleFreeInput = clone.querySelector('.js-content-ssfi')

                getDuplicateContent(subcontentSingleFreeInput, contentSingleFreeInput, ' ', false, ' ')

                let subcontentMultipleFreeInput = clone.querySelectorAll('.js-multiple-selection-free-input')
                let contentMultipleFreeInput = clone.querySelector('.js-content-msfi')

                getDuplicateContent(subcontentMultipleFreeInput, contentMultipleFreeInput, ' ')

                let subcontentMatrixSingleSelection = clone.querySelectorAll('.js-matrix-single-selection')
                let contentMatrixSingleSelection = clone.querySelector('.js-content-mss')

                getDuplicateContent(subcontentMatrixSingleSelection, contentMatrixSingleSelection, ' ')

                let subcontentSingleSelectionChild = clone.querySelectorAll('.js-single-selection-child')
                let contentSingleSelectionChild = clone.querySelector('.js-content-ssc')

                getDuplicateContent(subcontentSingleSelectionChild, contentSingleSelectionChild, ' ')

                let index = 0

                inputs.forEach((inputItem : any) => {
                  if(inputItem.name) {
                    inputClones[index] = inputItem.name
                    inputItem.classList.add('has-name')
                    inputItem.name = ''
                    index = index + 1
                  }
                })

                if(inputNumberDuplicate.length > 0) {
                  inputNumberDuplicate.forEach((item : any) => {
                    let itemElement = item as HTMLInputElement
                    itemElement.type = 'text'
                    setInputFilter(item, value => {
                      return /^[0-9]*$/.test(value)
                    })
                  })
                }

                let mainParentClone1 = subcontentItem.querySelectorAll('.js-clone-content')

                mainParentClone1.forEach((cloneItem : any, cloneIndex : number) => {
                  let inputs = cloneItem.querySelectorAll('.has-name')

                  inputs.forEach((inputItem : any, inputIndex : number) => {
                    inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                  })
                })

                if(autoResize.length > 0) {
                  autoResize.forEach((item : HTMLElement) => {
                    item.addEventListener('input', () => {
                      item.style.height = window.getComputedStyle(item).getPropertyValue('min-height')
                    })
                  })
                }

                if(questions.length > 0) {
                  questions.forEach((itemQuestion : any) => {
                    itemQuestion.classList.add('hide')
                  })
                }

                if(deleteClone) {
                  deleteClone.addEventListener('click', () => {
                    if(clone) {
                      clone.remove()
                    }
          
                    label = subcontentItem.querySelectorAll('.js-label')

                    const duplicateContent = subcontentItem.querySelectorAll('.js-duplicate-content')
                    let limit = subcontentItem.dataset.limit
          
                    label.forEach((labelItem : any, labelIndex : number) => {
                      if(mainLabel) {
                        labelItem.innerHTML = mainLabel + (labelIndex + 1)
                      } else {
                        labelItem.innerHTML = '' + (labelIndex + 1) + ''
                      }

                      if((labelIndex + 1) >= limit) {
                        addButton.classList.add('hide')
                      } else {
                        addButton.classList.remove('hide')
                      }
    
                      duplicateContent.forEach((duplicateItem : any) => {
                        if((labelIndex + 1) >= limit) {
                          duplicateItem.classList.add('hide')
                        } else {
                          duplicateItem.classList.remove('hide')
                        }
                      })
                    })

                    let mainParentClone2 = subcontentItem.querySelectorAll('.js-clone-content')

                    mainParentClone2.forEach((cloneItem : any, cloneIndex : number) => {
                      let inputs = cloneItem.querySelectorAll('.has-name')

                      inputs.forEach((inputItem : any, inputIndex : number) => {
                        inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                      })
                    })
                  })
                }

                if(radioClone && contentClone) {
                  radioClone.forEach((item : any) => {
                    item.addEventListener('click', () => {
                      let itemElement = item as HTMLInputElement
                      if(itemElement.value === '') {
                        contentClone.classList.add('active')
                      } else {
                        contentClone.classList.remove('active')
                      }
                    })
                  })
                }

                parentClickedElement.after(clone)

                label = subcontentItem.querySelectorAll('.js-label')

                const duplicateContent = subcontentItem.querySelectorAll('.js-duplicate-content')
                let limit = subcontentItem.dataset.limit

                label.forEach((labelItem : any, labelIndex : number) => {
                  if(mainLabel) {
                    labelItem.innerHTML = mainLabel + (labelIndex + 1)
                  } else {
                    labelItem.innerHTML = '' + (labelIndex + 1) + ''
                  }

                  if((labelIndex + 1) >= limit) {
                    addButton.classList.add('hide')
                  } else {
                    addButton.classList.remove('hide')
                  }

                  duplicateContent.forEach((duplicateItem : any) => {
                    if((labelIndex + 1) >= limit) {
                      duplicateItem.classList.add('hide')
                    } else {
                      duplicateItem.classList.remove('hide')
                    }
                  })
                })

                let getRadioButton = subcontentItem.querySelectorAll('input[type="radio"]')
                let itemElementChecked : any = []
                let indexChecked = 0

                getRadioButton.forEach((item : any) => {
                  let itemElement = item as HTMLInputElement
                  if(itemElement.checked) {
                    itemElementChecked[indexChecked] = itemElement
                    indexChecked = indexChecked + 1
                  }
                })

                let mainParentClone = subcontentItem.querySelectorAll('.js-clone-content')

                mainParentClone.forEach((cloneItem : any, cloneIndex : number) => {
                  let inputs = cloneItem.querySelectorAll('.has-name')
                  let inputSub = cloneItem.querySelectorAll('.has-subname')
                  let flexibleName = cloneItem.querySelectorAll('.flexible-name')

                  inputs.forEach((inputItem : any, inputIndex : number) => {
                    inputItem.name = inputClones[inputIndex].replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                  })

                  inputSub.forEach((inputItem : any) => {
                    inputItem.name = inputItem.name.replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                  })

                  flexibleName.forEach((inputItem : any) => {
                    inputItem.name = inputItem.name.replace(/(\d+)(?!.*\d)/g, String(cloneIndex + 1))
                  })
                })

                itemElementChecked.forEach((itemElement : any) => {
                  itemElement.checked = true
                })

                if(deleteButton) {
                  let deleteButtonCurrent = deleteButton[0].closest('.js-candidate-forms')

                  if(!deleteButtonCurrent) {
                    deleteButtonCurrent = deleteButton[0].closest('.form__select-content')
                  }

                  deleteButton.forEach((item: any) => {
                    item.addEventListener('click', () => {
                      let parent = item.closest('.js-subitem')

                      if(!parent) {
                        parent = item.closest('.js-duplicate')
                      }

                      parent.remove()

                      let mainDeleteParent = deleteButtonCurrent.querySelectorAll('.js-candidate-label')
                      let mainDeleteParentSecond = deleteButtonCurrent.querySelectorAll('.js-second-candidate-label')

                      if(mainDeleteParent.length <= 0) {
                        mainDeleteParent = deleteButtonCurrent.querySelectorAll('.js-label-ss')
                        mainDeleteParentSecond = deleteButtonCurrent.querySelectorAll('.js-label-second-ss')
                      }

                      mainDeleteParent.forEach((itemDelete: any, indexDelete : number) => {
                        itemDelete.innerHTML = itemDelete.innerHTML.replace(/(\d+)(?!.*\d)/g, String(indexDelete + 1))
                      })

                      if(mainDeleteParentSecond) {
                        mainDeleteParentSecond.forEach((itemDelete: any, indexDelete : number) => {
                          itemDelete.innerHTML = itemDelete.innerHTML.replace(/(\d+)(?!.*\d)/g, String(indexDelete + 1))
                        })
                      }
                    })
                  })
                }
              }
            }
          }
        })
      }
    })
  }
}

const flexibleContent = () => {
  let subcontent = document.querySelectorAll('.js-subcontent')
  let subcontentBill = document.querySelectorAll('.js-bill')
  let mainLabel = ' '

  if(subcontent) {
    getFlexibleContent(subcontent)
    getFlexibleContent(subcontentBill, true, mainLabel)
  }
}

export default flexibleContent