/**
* Toggles the select on radio active
*/

const toggleSelectOnRadio = () => {
  const radio = document.querySelectorAll('.js-classification-radio')
  const select = document.querySelectorAll('.js-classification-select')

  if(radio) {
    radio.forEach(item => {
      item.addEventListener('click', () => {
        let content = item.nextElementSibling

        select.forEach(selectItem => {
          selectItem.classList.add('disabled')
        })

        if(content) {
          content.classList.remove('disabled')
        }
      })
    })
  }
}

export default toggleSelectOnRadio